import forumobjct from "../script/forumobjct";
import essence from "./topics/essence";
import last from "./topics/last";
import index from "./topics/index";

export default {
  name: "topics",
  components: {
    essence,
    last,
    index
  },
  mixins: [forumobjct],
  data() {
    return {
      show_what: 'info',
      show_node_dialog: false
    }
  },
  created() {
    var type = this.$route.params.type;
    console.log('type:', type);
    if (!type) {
      type = 'index';
    }
    this.show_what = type;
  },
  methods: {
    on_click_show_node: function () {
      console.log('this.show_node_dialog:', this.show_node_dialog);
      this.show_node_dialog = true;
      this.$forceUpdate();
    },
    on_select_forum: function (forum) {
      var forum_url = '/forum/' + forum.fid;
      this.$router.push(forum_url);
    }
  },
  watch: {
    '$route'(to, from) {
      var type = this.$route.params.type;
      console.log('type:', type);
      if (!type) {
        type = 'index';
      }
      this.show_what = type;
      console.log('topics route change');
      this.$forceUpdate();
    }
  },
}
